import React, { useState } from "react";
import "@chatscope/chat-ui-kit-styles/dist/default/styles.min.css";
import {
  MainContainer,
  ChatContainer,
  MessageList,
  Message,
  MessageInput,
  ConversationHeader,
  Avatar,
  MessageSeparator,
  TypingIndicator,
  VoiceCallButton,
  VideoCallButton,
  InfoButton,
  Sidebar,
  ExpansionPanel,
} from "@chatscope/chat-ui-kit-react";

const Chat = ({ sessionID, setIsInterviewEnded }) => {
  const [messages, setMessages] = useState([
    {
      direction: "incoming",
      message: "Hello my friend",
      position: "single",
      sender: "AI Interviewer Boter",
      sentTime: "15 mins ago",
    },
  ]);
  const [isTyping, setIsTyping] = useState(false);
  const [showSideBar, setShowSideBar] = useState(false);
  const [sideBarContent, setSideBarContent] = useState("");
  const [hasCaseNotification, setHasCaseNotification] = useState(false);
  const handleSend = async (messageText) => {
    const userMessage = {
      direction: "outgoing",
      message: messageText,
      position: "single",
      sender: "You",
      sentTime: new Date().toLocaleTimeString(),
    };

    setMessages((prevMessages) => [...prevMessages, userMessage]);
    setIsTyping(true);
    console.log(sessionID)
    try {
      const response = await fetch(`${process.env.REACT_APP_BASE_URL}${process.env.REACT_APP_PROCESS_CHAT_MESSAGE}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          message: messageText,
          sessionid: sessionID
        }),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      const data = await response.json();
      console.log(data);
      console.log(hasCaseNotification);
      if (data.question_type === 'case' && !hasCaseNotification) {
        const caseNarrative = {
          direction: "incoming",
          message: "This is a case question, please refer to the case narrative in the side of the screen.",
          position: "single",
          sender: "AI Interviewer Boter",
          sentTime: new Date().toLocaleTimeString(),
        };
        setHasCaseNotification(true);
        setMessages((prevMessages) => [...prevMessages, caseNarrative]);
        setShowSideBar(true);
        setSideBarContent(data.case_narrative);
      } else if (data.question_type === 'complete') {
        setIsInterviewEnded(true);
      }

      const botMessage = {
        direction: "incoming",
        message: data.ai_message,
        position: "single",
        sender: "AI Interviewer Boter",
        sentTime: new Date().toLocaleTimeString(),
      };
      console.log(botMessage)
      setMessages((prevMessages) => [...prevMessages, botMessage]);
      setIsTyping(false);

    } catch (error) {
      console.error("Error sending message:", error);
      setMessages((prev) => [...prev, { sender: "bot", text: "Error: Unable to process message" }]);
    }

  };

  return (
    <div
      style={{
        position: "absolute",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
      }}
    >
      <MainContainer>
        <ChatContainer>
          <ConversationHeader>
            <Avatar
              name="AI Interviewer Boter"
              src="https://chatscope.io/storybook/react/assets/emily-xzL8sDL2.svg"
            />
            <ConversationHeader.Content
              userName="AI Interviewer Boter"
              info="Active 10 mins ago"
            />
            <ConversationHeader.Actions>
              <VoiceCallButton />
              <VideoCallButton />
              <InfoButton />
            </ConversationHeader.Actions>
          </ConversationHeader>
          <MessageList
            typingIndicator={
              isTyping ? <TypingIndicator content="AI Interviewer Boter is typing..." /> : null
            }
          >
            <MessageSeparator content="Today" />
            {messages.map((msg, index) => (
              <Message key={index} model={msg}>
                {msg.direction === "incoming" && (
                  <Avatar
                    name="AI Interviewer Boter"
                    src="https://chatscope.io/storybook/react/assets/emily-xzL8sDL2.svg"
                  />
                )}
              </Message>
            ))}
          </MessageList>
          <MessageInput
            placeholder="Type message here"
            onSend={handleSend}
          />
        </ChatContainer>

        {showSideBar && (
        <Sidebar position="right" style={{ height: "100%", width: "30%", display: "flex" }}>
          <ExpansionPanel open title="Case Narrative">
            <p>{sideBarContent}</p>
          </ExpansionPanel>
        </Sidebar>
        )}
      </MainContainer>
    </div>
  );
};

export default Chat;
