import React, { useState, useEffect } from "react";
import "./MockInterviewPage.css";
import { Chart as ChartJS, RadialLinearScale, PointElement, LineElement, Filler, Tooltip, Legend } from "chart.js";
import { Radar } from "react-chartjs-2";
import ButtonWithSpinner from "../../components/ButtonWithSpinner/ButtonWithSpinner";
import Chat from "../../components/Chat";
// Register required components
ChartJS.register(RadialLinearScale, PointElement, LineElement, Filler, Tooltip, Legend);

const MockInterviewPage = () => {
  const [resume, setResume] = useState("");
  const [jobDescription, setJobDescription] = useState("");
  const [numBehaviorQuestions, setNumBehaviorQuestions] = useState(1);
  const [numTechnicalQuestions, setNumTechnicalQuestions] = useState(1);
  const [numCaseQuestions, setNumCaseQuestions] = useState(1);
  const [maxFollowUps, setMaxFollowUps] = useState(1);
  const [evaluations, setEvaluations] = useState([]);
  const [isInterviewStarted, setIsInterviewStarted] = useState(false);
  const [isInterviewEnded, setIsInterviewEnded] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [sessionid, setSessionid] = useState("");
  const [isEvaluationReady, setIsEvaluationReady] = useState(false)

  // Base API URL from environment variable

  const genQuestions = async () => {
    const newSessionId = Date.now().toString(); // Generate session ID first
    setSessionid(newSessionId); // Update state
    console.log(sessionid)
    setIsLoading(true);
    try {
      const response = await fetch(`${process.env.REACT_APP_BASE_URL}${process.env.REACT_APP_GENERATE_INTERVIEW_QUESTIONS}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          job_description: jobDescription,
          resume: resume,
          num_tech_questions: numTechnicalQuestions,
          num_case_questions: numCaseQuestions,
          num_behavior_questions: numBehaviorQuestions,
          num_followup_questions: maxFollowUps,
          sessionid: newSessionId
        }),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      const data = await response.json();
      if (data.status) {
        setIsInterviewStarted(true)
        console.log(data.status)
      }

    } catch (error) {
      console.error("Error sending message:", error);
    } finally {
      setIsLoading(false);
    }
  };


  useEffect(() => {
    const evaluateInterview = async () => { //  Correct: Define async function inside useEffect
      if (!isInterviewEnded) return;  //  Only run if the interview has ended
      console.log(isInterviewEnded);
      try {
        const response = await fetch(`${process.env.REACT_APP_BASE_URL}${process.env.REACT_APP_EVALUATE_SESSION_ENDPOINT}`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            sessionid: sessionid,  //  Ensure sessionid is correctly passed
          }),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }

        const data = await response.json();
        setEvaluations([
          {
            questionAndAnswers: "Congratulations!",
            evaluation: data.evaluation,
          }
        ]);
        setIsEvaluationReady(true);


      } catch (error) {
        console.error("Error evaluating:", error);
      }
    };

    evaluateInterview(); //  Call the async function
  }, [isInterviewEnded, sessionid]); //  Runs when `isInterviewEnded` changes


  const RadarChartComponent = ({ evaluation }) => (
    <Radar
      data={{
        labels: Object.keys(evaluation).map((key) => key + " Score"),
        datasets: [
          {
            label: "Evaluation Scores",
            data: Object.values(evaluation).map((value) => value['Score']),
            backgroundColor: "rgba(0, 123, 255, 0.2)",
            borderColor: "#007bff",
            borderWidth: 1,
          },
        ],
      }}
      options={{
        scales: {
          r: {
            suggestedMin: 0,
            suggestedMax: 5,
          },
        },
      }}
    />
  );

  return (
    <div className="mock-interview-page">
      <h1 className="page-title">Mock Interview</h1>
      {/* Setup Section */}
      {!isInterviewStarted && (
        <div className="setup-section">
          <textarea
            className="input-area"
            placeholder="Paste your resume (optional)..."
            value={resume}
            onChange={(e) => setResume(e.target.value)}
          ></textarea>
          <textarea
            className="input-area"
            placeholder="Paste job description here..."
            value={jobDescription}
            onChange={(e) => setJobDescription(e.target.value)}
          ></textarea>
          <div className="settings">
            <label style={{ display: "block" }}>
              Number of Behavioral Questions:
            </label>

            <input
              className="number-input"
              type="number"
              min="0"
              value={numBehaviorQuestions}
              onChange={(e) => setNumBehaviorQuestions(parseInt(e.target.value))}
            />
            <label style={{ display: "block" }}>
              Number of Technical Questions:
            </label>

            <input
              className="number-input"
              type="number"
              min="0"
              value={numTechnicalQuestions}
              onChange={(e) => setNumTechnicalQuestions(parseInt(e.target.value))}
            />
            <label style={{ display: "block" }}>
              Number of Case Questions:
            </label>

            <input
              className="number-input"
              type="number"
              min="0"
              value={numCaseQuestions}
              onChange={(e) => setNumCaseQuestions(parseInt(e.target.value))}
            />
            <label style={{ display: "block" }}>
              Max Follow-Up Questions:
            </label>

            <input
              className="number-input"
              type="number"
              min="0"
              max="5"
              value={maxFollowUps}
              onChange={(e) => setMaxFollowUps(Math.min(5, parseInt(e.target.value)))}
            />
          </div>
          <ButtonWithSpinner isLoading={isLoading} onClick={() => { setSessionid(Date.now().toString()); genQuestions() }}>
            {isLoading ? "Loading..." : "Start Interview"}
          </ButtonWithSpinner>
        </div>
      )}

      {isInterviewStarted && !isEvaluationReady && (
        <div className="interview-section">
          <div className="App">
            <Chat sessionID={sessionid} setIsInterviewEnded={setIsInterviewEnded}/>
          </div>
        </div>
      )}


      {isEvaluationReady && (
        <div className="evaluation-section">
          <h2 className="section-title">Interview Complete</h2>

          {/* Map through evaluations */}
          {evaluations.map((evaluationItem, idx) => (
            <div key={idx} className="evaluation-block">
              <h3 className="evaluation-question">Evaluation Report</h3>

              {/* Show Overall Scores */}
              <div className="evaluation-scores">
                {Object.entries(evaluationItem.evaluation.Evaluation_Report).map(([key, value]) => (
                  <div key={key} className="evaluation-score">
                    <h4 className="score-title">
                      {key}: {value['Score']}/5
                    </h4>
                    <p className="score-rationale">
                      {value['Rationale'] || "No rationale provided."}
                    </p>
                  </div>
                ))}
              </div>

              {/* Radar Plot Visualization */}
              <div className="radar-plot">
                <RadarChartComponent evaluation={evaluationItem.evaluation.Evaluation_Report} />
              </div>

              {/* Detailed Question Review */}
              <div className="evaluation-cards-container">
                {evaluationItem.evaluation.Question_Review.Answer_Review.map((review, reviewIdx) => (
                  <div key={reviewIdx} className="evaluation-card">
                    <h4>Question {review.Question_Index}.{review.Question_SubIndex}</h4>
                    <p><strong>Question:</strong> {review.Question_Text}</p>
                    <p><strong>Your Answer:</strong> {review.Candidate_Answer}</p>
                    <p><strong>General Score:</strong> {review.General_Score}</p>
                    <p><strong>Strength:</strong> {review.Strength}</p>
                    <p><strong>Weakness:</strong> {review.Weakness}</p>
                    <p><strong>Advice:</strong> {review.Improve_Advice}</p>
                    <p><strong>Sample Answer:</strong> {review.Sample_Answer}</p>
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
      )}

    </div>
  );
};

export default MockInterviewPage;
